module.exports = "/* Intro */\n\n#intro {\n    padding: 2em 0;\n}\n\n.hero {\n    margin-top: 0;\n    display: flex;\n    justify-content: space-between;\n}\n\n.about-me {\n    margin: 2em 0;\n    width: 50%;\n}\n\n.about-me>p.sub-text {\n    font-size: .8em;\n    font-weight: 200;\n    color: #484848;\n    margin: 0;\n}\n\n.about-me>p.main-text {\n    font-size: 1.1em;\n    margin: 0 0 2.2em 0;\n}\n\n.about-me>p.main-text>em {\n    border-radius: 4px;\n    color: #555;\n    background: #f8f8f8;\n}\n\n.secondary-text {\n    font-size: .8em;\n    font-weight: 200;\n    color: #484848;\n}\n\n.hero-picture {\n    /* TEMP:TODO */\n    width: 365px;\n    height: 330px;\n    background: #006BE8;\n    border-radius: 30px;\n}\n\n/* portfolio */\n\nsection#portfolio {\n    width: 100%;\n    padding: 2em;\n    background: #222222;\n    padding-bottom:4em;\n}\n\n#portfolio>div>header {\n    text-align: center;\n}\n\n#portfolio>div>header>h3 {\n    font-size: 1.2em;\n    color: #FFFFFF;\n    line-height: 0em;\n}\n\n#portfolio>div>header>p {\n    font-weight: 300;\n    font-size: .8em;\n    color: #E6E6E6;\n}\n\n.portfolio-container {\n    display: flex;\n    flex-direction: column;\n}\n\n.portfolio-item {\n    display: flex;\n    flex-direction: row;\n    height: 18em;\n}\n\n.portfolio-item__alternate {\n    flex-direction: row-reverse;\n}\n\n.portfolio-item__alternate>.portfolio-item__heading {\n    border-radius: 0px 30px 30px 0px;\n}\n\n.portfolio-item__alternate>.portfolio-item__preview {\n    border-radius: 30px 0px 0px 30px;\n}\n\n.portfolio-item__heading {\n    width: 20em;\n    background: white;\n    border-radius: 30px 0px 0px 30px;\n    padding: 1.5em;\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n}\n\n.portfolio-item__preview {\n    width: calc(100% - 20em);\n    background: #191919;\n    border-radius: 0px 30px 30px 0px;\n}\n\n.portfolio-item__title {\n    font-size: 1.4em;\n    font-weight: 500;\n}\n\n.portfolio-item__description {\n    font-size: 0.7em;\n}\n\n.portfolio-item__subtitle {\n    padding-bottom: 0.7em;\n    font-size: 0.6em;\n}\n\n.portfolio-item__preview {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.portfolio-item__preview>img {\n    height: 10em;\n}\n\n@media screen and (max-width: 1100px) {\n    .about-me  {\n        width: 80%;\n    }\n}\n\n@media screen and (max-width: 1000px) {\n    .portfolio-item__heading{\n        width:15em;\n    }\n\n    .portfolio-item__preview {\n        width: calc(100% - 10em);\n    }\n\n\n    \n}\n\n@media screen and (max-width: 500px) {\n\n    .portfolio-container {\n        padding:0;\n    }\n    .portfolio-item   {\n        flex-direction: column-reverse;\n    }\n    \n    .portfolio-item__heading,.portfolio-item__preview{\n        width:100%;\n    }\n\n    .portfolio-item__preview {\n        background:none;\n    }\n\n    .portfolio-item__preview > img {\n        width:100%;\n        height:100%;\n    }\n\n    .portfolio-item__heading {\n        border-radius:0;\n        padding-bottom:4em;\n    }\n\n}\n/*# sourceMappingURL=data:application/json;base64, */"